.footer {
  padding-bottom: 64px;

  @include on-tablet {
    padding-bottom: 120px;
  }
}

.contacts {
  font-weight: 400;

  @include page-grid;

  @include on-desktop {
    margin-top: 104px;
  }
}

.contact {
  display: grid;
  gap: 8px;
  align-content: start;

  &__phone,
  &__email,
  &__adress {
    text-decoration: none;
    color: $light-text-color;
    font-size: 14px;
    letter-spacing: 0.15px;
    margin-bottom: 0;
    margin-left: 8px;
    margin-top: 16px;

    @include on-desktop {
      grid-column: 8 / 12;
    }

  }

  &__call-us,
  &__write-us,
  &__find-us {
    text-decoration: none;
    color: $dark-text-color;
    margin-bottom: 0;
    width: 200px;
    transition: transform 0.3s, font-weight 0.3s, color 0.3s;


    @include on-desktop {
      grid-column: 9 / 12;
    }

    &:hover {
      cursor: pointer;
      color: $dark-text-color;
      font-weight: 600;
    }


  }
}
