.icon {
  display: block;
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  transition: transform 0.3s;
  position: relative;

  @include hover(transform, scale(1.2));

  @include on-tablet {
    height: 32px;
    width: 32px;
    background-size: contain;
  }

  &--phone {
    background-image: url(../icons/icon-phone.svg);

    position: relative;

    &__number {
      display: none;

      @include on-desktop {
        opacity: 0;
        display: inline; 
        text-decoration: none;
        position: absolute;
        top: 6px;
        right: 40px;
        letter-spacing: 2px;
        white-space: nowrap;
        font-size: 12px;
        color: $active-color;
        transition: opacity 0.3s;
        pointer-events: none;
      }
    }

    &:hover .icon--phone__number {
      opacity: 1;
    }
  }

  &--menu {
    background-image: url(../icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../icons/icon-close.svg);
    margin-right: 12px;
  }
}
